@import url(https://fonts.googleapis.com/css2?family=Codystar&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Slab&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    scroll-behavior: smooth;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', 'Codystar', 'Josefin Slab', monospace;
}
    .nav {
        position: fixed;
        z-index: 50;
        background-color: #1B1C1B;
        left: 0;
        right: 0;
        height: 45px;
        display: flex;
        flex-direction: row;
    }
    
    .nav-heading {
        color: white;
        line-height: 40px;
        font-size: 35px;
        padding-left: 25px;
        width: 100px;
        cursor: pointer;
    }
    
    .box {
        display: flex;
        width: 500px;
        height: 100%;
        margin-left: auto;
    }
    
    li {
        display: flex;
        flex: 1 1;
        font-family: 'Josefin Slab';
        font-weight: bold;
        justify-content: center;
        margin: auto auto;
    }
    
    .link {
        flex: 1 1;
        display: flex;
        margin: auto auto;
        height: 70%;
        font-size: 14px;
        border-radius: 8px;
        color: white;
        cursor: pointer;
        transition: all 0.2s;
    }
    
    .active-home {
        color: black;
        background-color: #0EA396;
        font-size: 18px;
        box-shadow: 5px 5px 5px #0ea39781
    }
    
    .active-details {
        color: black;
        background-color: #08AE7C;
        font-size: 18px;
        box-shadow: 5px 5px 5px #08ae7c81
    }
    
    .active-overview {
        color: black;
        background-color: #ACA142;
        font-size: 18px;
        box-shadow: 5px 5px 5px #ACA14281
    }
    
    .active-improvement {
        color: black;
        background-color: #AE8D08;
        font-size: 18px;
        box-shadow: 5px 5px 5px #AE8D0881
    }
    
    .icon {
        position: absolute;
        top: 5px;
        right: 20px;
        z-index: 20;
    }
    
    @media (min-width: 651px) {
        .icon {
            display: none;
        }
    }
    
    @media (max-width: 650px) {
        .box {
            display: block;
            flex-direction: column;
            background-color: black;
            height: 100vh;
            transition: width 0.2s;
        }
        .link:nth-child(1) {
            margin-top: 25vh;
        }
        .link {
            height: 10vh;
            width: 100%;
            display: flex;
            border-radius: 0;
        }
    }
.details-content {
    position: relative;
    background-color: #08AE7C;
    min-height: 102vh;
}

.details-body {
    display: flex;
    width: 100%;
    margin-top: 5vh;
    justify-content: center;
    height: 60vh;
}

.circle {
    position: absolute;
    right: 20px;
    top: 35%;
    z-index: 20;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    background-color: #058f65;
    color: white;
    text-align: center;
    cursor: pointer;
    font-size: 16px;
    line-height: 38px;
    font-weight: bold;
    transition: width 0.1s ease-in-out, box-shadow 0.1s ease-in-out;
    overflow: hidden;
}

.circle:hover span {
    display: none;
}

.circle:hover {
    width: 120px;
    border-radius: 6px;
    box-shadow: 5px 5px rgba(0, 0, 0);
}

.circle:after {
    opacity: 0;
    content: '';
}

.circle:hover:after {
    opacity: 1;
    content: attr(cntnt);
}

.details-data {
    display: flex;
    flex-direction: column;
    padding: 10px;
    flex: 1 1;
    margin-top: -8vh;
}

.details-heading {
    font-family: 'Codystar';
    text-align: center;
    font-size: 80px;
    margin-top: 7.5vh;
    -webkit-user-select: none;
    -moz-user-select: none;
    -o-user-select: none;
    -ms-user-select: none;
        user-select: none;
}

.details-subheading {
    font-family: 'Josefin Slab';
    text-align: left;
    font-size: 22px;
    margin: auto;
    display: flex;
    flex-direction: column;
}

.details-subheading2 {
    font-family: 'Josefin Slab';
    flex: 1 1;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0.9);
    width: 100%;
    transition: top 250ms ease-out;
    transition-delay: 500ms;
}

.details-data2 {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 24%;
    left: 0;
    right: 0;
    padding: 1.5em;
    bottom: 10%;
    z-index: 2;
    background-color: #19312ae1;
    vertical-align: middle;
    color: white;
    text-align: center;
    transition: right 500ms ease-out, bottom 500ms ease-out;
}

.elem {
    padding-top: 0.5em;
    line-height: 26px;
}
.slideshow-content {
    margin: 5px;
    display: flex;
    flex: 1 1;
    max-width: 600px;
    justify-self: center;
}

.slideshow-image.slideshow-left {
    width: 20%;
    opacity: 65%;
    padding: 5px;
    max-height: 50vh;
    display: flex;
    transition: all 500ms;
}

.slideshow-image.slideshow-main {
    opacity: 100%;
    padding: 5px;
    max-height: 50vh;
    display: flex;
    transition: all 500ms;
}

.slideshow-image.slideshow-right {
    width: 20%;
    opacity: 65%;
    padding: 5px;
    max-height: 50vh;
    display: flex;
    transition: all 500ms;
}

img {
    max-width: 100%;
    max-height: 50vh;
    height: auto;
    align-self: center;
    -webkit-user-select: none;
    -moz-user-select: none;
    -o-user-select: none;
    -ms-user-select: none;
        user-select: none;
}

.empty {
    max-width: 100%;
    height: auto;
    align-self: center;
}

.slideshow-icon {
    text-align: center;
    vertical-align: center;
    display: flex;
    align-items: center;
    z-index: 18;
}

svg {
    transition: all 180ms;
    cursor: pointer;
}

.slideshow-image {
    display: none;
    opacity: 0%;
}
.home-content {
    background-color: #0EA396;
    min-height: 100vh;
}

.home-text {
    background-color: rgba(255, 255, 255, 0.48);
    font-family: 'Josefin Slab';
    padding: 50px 100px 50px 100px;
}

.home-heading {
    font-size: 32px;
    padding: 20px;
}

.home-subheading,
.home-subheading-button {
    display: flex;
    flex: 1 1;
    font-size: 18px;
}

.home-subheading {
    font-size: 22px;
}

.home-button {
    text-align: center;
    font-family: 'Josefin Slab';
    margin-left: auto;
    margin-right: auto;
    background-color: #17573D;
    width: 230px;
    height: 70px;
    display: flex;
    border-radius: 2px;
    justify-content: center;
    margin-top: 50px;
    cursor: pointer;
    transition: all 0.2s;
}

.home-button:hover {
    transform: scale(1.1);
    box-shadow: 5px 10px rgba(0, 0, 0);
}

@media (max-width: 700px) {
    .home.buttonText {
        font-size: 14px;
        line-height: 50px;
    }
    .home-heading {
        font-size: 18px;
        padding: 10px;
    }
    .home-subheading {
        font-size: 18px;
        flex-direction: column;
        padding: 0px;
    }
    .home-subheading-button {
        font-size: 14px;
        padding: 5px;
    }
    .home-text {
        padding: 20px;
    }
    .home-button {
        width: 180px;
        height: 50px;
    }
}
.content {
    background: linear-gradient(45deg, #119DA4, #04B46D, #FBF33C);
    height: 99.5vh;
}

.text {
    width: 96%;
    height: 100%;
    margin: auto auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: -2%;
}

.heading {
    font-family: 'Roboto';
    text-align: right;
}

.subheading {
    font-family: 'Roboto';
    text-align: right;
}

.button {
    text-align: center;
    font-family: 'Josefin Slab';
    margin-left: auto;
    margin-right: auto;
    background-color: #5A730D;
    width: 220px;
    height: 70px;
    display: flex;
    border-radius: 8px;
    justify-content: center;
    margin-top: 50px;
    transition: all 150ms;
}

.button:hover {
    transform: scale(1.1);
    box-shadow: 5px 10px rgba(0, 0, 0);
}

.buttonText {
    display: inline-flex;
    font-family: 'Josefin Slab';
    font-size: 22px;
    line-height: 70px;
    color: #9AF188;
    cursor: pointer;
}

.buttonImage {
    margin-left: 10px;
    cursor: pointer;
    transition: all 150ms;
}

.buttonImage:hover {
    /* Firefox */
    /* Chrome, Opera 15+, Safari 3.1+ */
    /* IE 9 */
    transform: rotate(90deg) scale(1.5);
    /* Firefox 16+, IE 10+, Opera */
}

@media (max-width: 650px) {
    .button {
        text-align: center;
        font-family: 'Josefin Slab';
        margin-left: auto;
        margin-right: auto;
        background-color: #5A730D;
        width: 150px;
        height: 50px;
        display: flex;
        border-radius: 8px;
        justify-content: center;
        margin-bottom: -10%;
        margin-top: 10%;
        transition: all 150ms;
    }
    .buttonText {
        display: inline-flex;
        font-family: 'Josefin Slab';
        font-size: 16px;
        line-height: 50px;
        color: #9AF188;
        cursor: pointer;
    }
    .buttonImage {
        margin-left: 10px;
        cursor: pointer;
        height: 25px;
        transition: all 150ms;
    }
}
.overview-content {
